.btn {
	position: relative;
	padding: 3px 20px;
	display: flex;
	align-items: center;
	border: 2px solid #ffffff;
	white-space: nowrap;
	overflow: hidden;
	transition: .5s;
	box-sizing: border-box;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: -1px;
		width: 100%;
		height: 0;
		background-color: #fff;
		transition: .8s;
		z-index: -1;
		border-radius: 0 0 50% 50%; }
	svg {
		margin-right: 15px; }
	&:hover {
		border: 2px solid darken($accent, 0);
		color: $accent;
		cursor: pointer;
		box-sizing: border-box;
		&::before {
			height: 190%; }
		svg {
			use {
				fill: $accent; } } } }

.btn-empty {
	position: relative;
	padding: 3px 20px;
	display: flex;
	align-items: center;
	white-space: nowrap;
	overflow: hidden;
	transition: .5s;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 100%;
		height: 0;
		background-color: #fff;
		transition: .8s;
		z-index: -1;
		border-radius: 50% 50% 0 0; }
	&:hover {

		color: $accent;
		cursor: pointer;
		&::before {
			height: 190%; }
		svg {
			use {
				fill: $accent; } } } }
.btn-fill {
	position: relative;
	display: flex;
	align-items: center;
	white-space: nowrap;
	justify-content: center;
	background-color: $accent;
	color: #fff;
	overflow: hidden;
	transition: .5s;
	border: 1px solid $accent;
	z-index: 1;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 100%;
		height: 0;
		background-color: #fff;
		transition: .8s;
		border-radius: 50% 50% 0 0;
		box-sizing: border-box;
		z-index: -1; }
	&:hover {
		border: 1px solid $accent;
		box-sizing: border-box;
		color: $accent;
		cursor: pointer;
		&::before {
			box-sizing: border-box;
			height: 190%; }
		svg {
			use {
				fill: #fff; } } } }

.btn-green-border {
	position: relative;
	padding: 3px 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 2px solid $accent;
	white-space: nowrap;
	overflow: hidden;
	font-size: 14px;
	font-weight: 500;
	letter-spacing: 0.7px;
	color: $accent;
	transition: .5s;
