		background-color: $accent;
		transition: .8s;
		z-index: -1;
		border-radius: 0 0 50% 50%; }
	svg {
		margin-right: 15px; }
	&:hover {
		border: 2px solid darken($accent, 0);
		color: #fff;
		cursor: pointer;
		box-sizing: border-box;
		&::before {
			height: 210%; }
		svg {
			use {
				fill: $accent; } } } }
