.menu-section.active-js {
	opacity: 1;
	visibility: visible;
	display: flex;
	animation: menu-animate 0.6s ease-out; }


@keyframes menu-animate {
	0% {
		top: 10%;
		transform: scale(0.9); }
	50% {
		top: 0;
		transform: scale(0.9); }
	100% {
		transform: scale(1); } }




.ttopMenuBlock-active {
	animation: topMenuBlock-animate 0.5s ease-in-out; }


@keyframes topMenuBlock-animate {
	0% {
		transform: rotateX(50deg); }
	100% {
		transform: rotateX(0deg); } }

.btottomMenuBlock-active {
	animation: bottomMenuBlock-animate 0.7s ease-in-out 0.3s; }


@keyframes bottomMenuBlock-animate {
	0% {
		opacity: 0;
		transform: translateY(100%); }
	100% {
		opacity: 1;
		transform: translateY(0%); } }

@keyframes down-main-title {
	from {
		transform: translate(-50%, -20%);
		opacity: 0; }
	to {
		transform: translate(-50%, 0%);
		opacity: 1; } }
.menu-section {
	position: fixed;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	overflow: hidden;
	z-index: 998;
	opacity: 0;
	visibility: hidden;
	transition: 0.5s;
	display: none;
	flex-direction: column;
	@include to(1200px);

	.menu-block-top {
		flex-basis: 50%;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		background: url('../img/bg-menu.jpg') center center no-repeat;
		background-size: cover;
		z-index: -2;
		@include to(1200px) {
			flex-basis: 30%; }
		.menu__word {
			padding-top: 80px;
			position: relative;
			font-size: 30px;
			font-weight: 300;
			line-height: 60px;
			text-transform: uppercase;
			letter-spacing: 2.1px;
			color: #fff;
			text-transform: uppercase;
			@include to(1200px) {
				font-size: 27px; }
			@include to(1200px) {
				font-size: 23px; }
			&::before {
				position: absolute;
				top: 78%;
				left: -55px;
				transform: translateY(-50%);
				content: '';
				display: inline-block;
				width: 40px;
				height: 34px;
				background: url('../img/svg/leaves-w-w.svg') center center no-repeat; } }
		&::before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-color: rgba(0,0,0,0.6);
			z-index: -1; } }

	.menu-block-bottom {
		flex-basis: 50%;
		position: relative;
		background: url('../img/bg-menu-bottom.jpg') bottom center no-repeat;
		background-size: cover;
		z-index: -2;
		@include to(1200px) {
			flex-basis: 70%;
			overflow-y: auto; }
		.menu-wrap {
			font-weight: 400;
			line-height: 42px;
			font-size: 20px;
			letter-spacing: 0.2px;
			a {
				color: #fff; }
			.header-menu {
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;
				transform: translateY(-70px);
				z-index: 20;
				@include to(1200px) {
					padding: 30px 20px;
					transform: translateY(0px); }
				@include to(520px) {
					padding: 20px 10px; }

				.menu-number {
					display: none;
					@include to(1200px) {
						flex-basis: 33%;
						display: block;
						text-align: left;
						.btn--header {
							svg {
								fill: $wcolor; }
							&:hover {
								svg {
									fill: $accent; } } }
						.tellink {
							display: block;
							text-align: center;
							margin-bottom: 20px; }
						.btn {
							margin: 0 auto;
							max-width: 200px;
							text-align: center; } }
					@include to(768px) {
						flex-basis: 100%;
						text-align: center; } }

				.has-children {
					user-select: none;
					@include to(1200px) {
						margin-bottom: 30px;
						flex-basis: 33%; }
					@include to(768px) {
						flex-basis: 100%;

						&:hover {
							cursor: pointer; } }
					.menu__title {
						margin-bottom: 65px;
						display: block;
						font-size: 20px;
						color: $wcolor;
						text-transform: uppercase;
						@include to(1200px) {
							margin-bottom: 0; }
						@include to(768px) {
							font-size: 18px;
							position: relative;

							&::after {
								content: "";
								display: block;
								top: 40%;
								right: 20px;
								position: absolute;
								width: 15px;
								height: 15px;
								border-left: 2px solid #fff;
								border-bottom: 2px solid #fff;
								transform: translateY(-50%) rotate(-45deg);
								@include to(520px) {
									right: 0px; } } } }
					ul.sub-menu {
						list-style: square outside;
						user-select: none;
						@include to(768px) {
							display: none; }
						li {
							position: relative;
							padding-left: 35px;
							@include to(1200px) {
								padding-left: 15px; }
							&::before {
								content: '';
								width: 3px;
								height: 3px;
								position: absolute;
								top: 50%;
								left: 0;
								transform: translateY(-50%);
								background-color: #fff; }
							a {
								font-size: 16px;
								letter-spacing: 0.16px;
								font-weight: 300;
								position: relative;
								color: #fff;
								font-size: 16px;
								font-weight: 400;
								line-height: 30px;
								letter-spacing: 0.4px;
								@include to(768px) {
									display: block;
									padding: 10px 0; }
								&::after {
									position: absolute;
									bottom: -8px;
									left: 0;
									content: '';
									display: block;
									height: 1px;
									width: 100%;
									background-color: #fff;
									transform: scale(0); }
								&:hover {
									&::after {
										transition: 0.4s;
										transform: scale(1); } } } } } } } } } }
